*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}
html{
  scroll-behavior : smooth;
}
body{
  font-family: 'Poppins', sans-serif;
  background:#f5f5f5;
}
section{
  padding: 3rem 0;
}
.section-info{
  padding: 1rem 0;
  text-align: center;
}
.section-title{
  font-size: 3rem;
}
.section-desc{
  color: #4e4e4e;
}
/* font */
@font-face {
  font-family: Poppins;
  src: url('./assets/fonts/Poppins-Regular.ttf');
}
/* utilities */
.d-flex{
  display: flex;
}
.justify-content-between{
  justify-content: space-between;
}
.align-items-center{
  align-items: center;
}
.container{
  max-width: 1200px;
  margin: 0 auto;
}
[class^='col-']{
  padding: 1rem;
}
.row{
  display: flex;
  flex-wrap: wrap;
}
.col-4{
  width: 33.33%;
}
.col-3{
  width: 25%;
}
.img-fluid{
  width: 100%;
  height: auto;
}

span{
  color: #00b33c;
}
